import { useState } from 'react'
import {Table, Form, Row, Col} from 'react-bootstrap'


export default (
  {priceTable}
) => {
  const initialState = {
    itemNumber: '1',
    itemCount: 1
  }
  const [state, setState] = useState(initialState);

  const onChangeNumber = (e) => {
    setState({...state, itemNumber: e.target.value})
  }
  const onChangeCount = (e) => {
    setState({...state, itemCount: e.target.value})
  }
  const onSelectItem = (e) => {
    console.log(e, e.target.value)
    setState({...state, itemNumber: e.target.value})
  }

  let item = priceTable.get(state.itemNumber);
  if (!item) {
      item = {
        number: 0,
        name: '',
        type: '',
        price: 0
      }
  }

  const itemCount = isNaN(parseInt(state.itemCount)) ? 0 : state.itemCount;
  const totalPrice = (item.price * itemCount);

  const selectOptions = Array.from(priceTable.values())
    .map(item => {
      return <option key={item.number} value={item.number}>{item.name}{item.type ? `(${item.type})` : ''}</option>
    })

  return (
    <>
      <div style={{height: '18rem', width: '100%'}}>
        <Table variant="dark" bordered>
          <tbody>
            <tr>
              <td style={{width: '8rem'}}>상품</td>
              <td>{item.name}</td>
            </tr>
            <tr>
              <td style={{width: '8rem'}}>규격</td>
              <td>{item.type}</td>
            </tr>
            <tr>
              <td style={{width: '8rem'}}>가격</td>
              <td>{item.price.toLocaleString()}</td>
            </tr>
            <tr>
              <td style={{width: '8rem'}}>총액</td>
              <td>{totalPrice.toLocaleString()}</td>
            </tr>
            <tr>
              <td style={{width: '8rem'}}>부가세 포함</td>
              <td>{(totalPrice * 1.1).toLocaleString()}</td>
            </tr>
          </tbody>
        </Table>
      </div>
      <Form>
        <Form.Group as={Row} className="mb-3">
          <Form.Label column xs={4}>
            상품 선택
          </Form.Label>
          <Col xs={8}>
            <Form.Select aria-label="상품 선택" defaultValue={'1'} onChange={onSelectItem}>
              {
                Array.from(priceTable.values())
                  .map(option => 
                    <option
                        key={option.number}
                        value={option.number}
                      >{option.name}{option.type !== '' ? ` - ${option.type}` : ''}
                    </option>
                  )
              }
            </Form.Select>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3">
          <Form.Label column xs={4}>
            상품 번호
          </Form.Label>
          <Col xs={8}>
            <Form.Control
              type="number"
              size="lg"
              placeholder="견적서 상품 번호"
              value={state.itemNumber}
              onChange={onChangeNumber}
            />
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3">
          <Form.Label column xs={4}>
            상품 갯수
          </Form.Label>
          <Col xs={8}>
            <Form.Control
              type="number"
              size="lg"
              placeholder=""
              value={state.itemCount}
              onChange={onChangeCount}
            />
          </Col>
        </Form.Group>
      </Form>
    </>
  );
};
