import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import { useState } from 'react';
import PriceForm from './component/PriceForm'

import priceFile from './price.tsv'

const loadPrice = () => {
  return fetch(priceFile)
  .then(r => r.text())
  .then(text => {
    const items = text.split('\n')
      .map(line => {
        const sp = line.split('\t');
        const number = sp[0];
        if (isNaN(parseInt(sp[0]))) {
          return null;
        }

        return {
          number: number,
          name: sp[1],
          type: sp[2],
          price: parseInt(sp[3].replace(/,/g, ''))
        }
      })
      .filter(item => item !== null);

    const result = new Map();
    for (const item of items) {
      result.set(item.number, item);
    }

    return result;
  })
}

const priceTable = await loadPrice();

function App() {
  return (
    <div className="App">
      <div className="App-header">
        <PriceForm priceTable={priceTable}/>
      </div>
    </div>
  );
}

export default App;
